"use client";

import { useState } from "react";
import { useRouter, useParams } from "next/navigation";
import api from "@/lib/axios";

export default function AddMenuItemPage() {
  const router = useRouter();
  const { restaurantId } = useParams();

  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState({
    name: "",
    description: "",
    price: "",
    image: "",
  });

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setForm({
      ...form,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      await api.post("/menu", {
        ...form,
        price: Number(form.price),
        restaurantId,
      });

      alert("Menu item added successfully!");
      router.push(`/menu/restaurant/${restaurantId}`);
    } catch (err) {
      console.error("Error adding menu item", err);
      alert("Failed to add menu item");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-3xl mx-auto p-6">
      <h1 className="text-4xl font-bold text-black mb-6">
        Add <span className="text-orange-600">Menu Item</span>
      </h1>

      <form
        onSubmit={handleSubmit}
        className="bg-white shadow-xl border rounded-2xl p-8 space-y-6"
      >
        {/* Name */}
        <div>
          <label className="block text-black font-semibold mb-1">
            Item Name
          </label>
          <input
            type="text"
            name="name"
            value={form.name}
            onChange={handleChange}
            required
            className="w-full border rounded-xl px-4 py-3 text-black focus:outline-none focus:ring-2 focus:ring-orange-500"
          />
        </div>

        {/* Description */}
        <div>
          <label className="block text-black font-semibold mb-1">
            Description
          </label>
          <textarea
            name="description"
            value={form.description}
            onChange={handleChange}
            required
            rows={3}
            className="w-full border rounded-xl px-4 py-3 text-black focus:outline-none focus:ring-2 focus:ring-orange-500"
          />
        </div>

        {/* Price */}
        <div>
          <label className="block text-black font-semibold mb-1">
            Price (â‚¹)
          </label>
          <input
            type="number"
            name="price"
            value={form.price}
            onChange={handleChange}
            required
            className="w-full border rounded-xl px-4 py-3 text-black focus:outline-none focus:ring-2 focus:ring-orange-500"
          />
        </div>

        {/* Image URL */}
        <div>
          <label className="block text-black font-semibold mb-1">
            Image URL
          </label>
          <input
            type="text"
            name="image"
            value={form.image}
            onChange={handleChange}
            required
            className="w-full border rounded-xl px-4 py-3 text-black focus:outline-none focus:ring-2 focus:ring-orange-500"
          />
        </div>

        {/* Submit Button */}
        <button
          type="submit"
          disabled={loading}
          className="w-full bg-gradient-to-r from-orange-500 to-orange-600 text-white py-4 rounded-2xl text-lg font-bold hover:opacity-90 transition disabled:opacity-60"
        >
          {loading ? "Adding Menu Item..." : "Add Menu Item"}
        </button>
      </form>
    </div>
  );
}
